const React = require('react');
const ReactDOM = require('react-dom');
const https = require('https');
//const http = require('http');
const PureRenderMixin = require('react-addons-pure-render-mixin');

const koopRepo = require('../../../backend/expressions');
const cacheRepo = require('../../../backend/cache');

const lawlyBot = require('../../../backend/lawly-bot');

const ToC = require('./toc.jsx');
const LoadingView = require('./loading.jsx');
const ErrorView = require('./error.jsx');
const DocContentView = require('./doc-content.jsx');

//const URL_BASE = "http://127.0.0.1:5984/bwb-ng/";
const onHtmlReceived = function (_this) {
    return function (body) {
        if (!!body) {
            if (_this.isMounted()) {
                _this.setState({
                    htmlSnippet: body,
                    loadingToc: true,
                    loadingDoc: false,
                    toc: null,
                    error: null
                });
            }
        } else {
            const bwbId = _this.props.bwbId;
            const expressionId = _this.props.version;

            console.error("Something went wrong loading " + bwbId, expressionId);
            if (_this.isMounted()) {
                _this.setState({
                    error: e,
                    loadingToc: false,
                    loadingDoc: false,
                    htmlSnippet: null,
                    toc: null
                });
            }
        }
    };
};

const notifyHtmlNotFound = function (bwbId, version) {
    var options = {
        hostname: lawlyBot.URL_LAWLY_BOT_HOST,
        port: 443,
        path: lawlyBot.URL_LAWLY_BOT_API_PATH + 'cache_miss',
        method: 'POST',
        headers: {
            'Content-Type': 'application/json'
        }
    };
    var req = https.request(options, function (res) {
        //console.log('Status: ' + res.statusCode);
        //console.log('Headers: ' + JSON.stringify(res.headers));
        //res.setEncoding('utf8');
    });
    req.on('error', function (e) {
        console.error('Problem with notifying repo miss: ' + e.message);
    });
    //req.write('{"string": ' + result + '}');
// write data to request body
    const body = JSON.stringify({bwbId: bwbId, version: version});
    //console.log("Writing " + body);
    req.write(body);
    req.end();
};

function throwError(error) {
    console.error(error);
    throw (error);
}

function hasClass(element, cls) {
    return (' ' + element.className + ' ').indexOf(' ' + cls + ' ') > -1;
}

const CHILD_CUT_OFF = 5;
function getBwbTitleElement(root) {
    var children = root.children;
    var cutoff = Math.min(CHILD_CUT_OFF, children.length);

    for (var i = 0; i < cutoff; i++) {
        var child = (children[i]);
        if (hasClass(child, 'kop')) {
            return child;
        }
    }
    return null;
}
function parseTitle(root) {
    var label = [];
    var sublabel = [];
    var children = root.children;

    for (var i = 0; i < children.length; i++) {
        var child = children[i];
        if (hasClass(child, 'label') || hasClass(child, 'nr')) {
            label.push(child.innerText.trim());
        } else if (hasClass(child, 'titel')) {
            sublabel.push(child.innerText.trim());
        }
    }

    return {
        label: label.join(' '),
        sublabel: sublabel.length > 0 ? sublabel.join(' ') : undefined
    }
}
function generateToc(root) {
    var children = root.children;

    var entries = [];
    for (var i = 0; i < children.length; i++) {
        var child = (children[i]);
        var titleEl = getBwbTitleElement(child);
        if (titleEl) {
            var title = parseTitle(titleEl);
            var entry = {
                label: title.label,
                sublabel: title.sublabel,
                id: child.attributes['id'].value,
                children: generateToc(child)
            };
            entries.push(entry);
        } else {
            // If this is not a item in the ToC, maybe some descendant will be
            entries = entries.concat(generateToc(child));
        }
    }

    return entries;
}

const DocReader = React.createClass({
    mixins: [PureRenderMixin],

    propTypes: {
        bwbId: React.PropTypes.string.isRequired,
        version: React.PropTypes.string.isRequired
    },

    componentDidUpdate: function (prevProps, prevState) {
        console.log("Reader component updated");

        if (this.state.loadingToc) {
            var doc = document.querySelector('.legal-document');
            if (doc) {
                var toc = generateToc(doc);
                if (toc.length < 1) console.error("Warning: ToC was empty");
                if (this.isMounted()) {
                    this.setState({
                        loadingToc: false,
                        loading: false,
                        toc: toc,
                        error: null
                    });
                }
            } else {
                throw new Error("Could not find document content.");
            }
        }
    },

    componentDidMount: function () {
        const bwbId = this.props.bwbId;
        const expressionId = this.props.version;

        // Get html
        var _this = this;

        cacheRepo.fetchHtml(bwbId, expressionId)
            .then(onHtmlReceived(_this))
            .catch(function (err) {
                console.error(err);
                if (err.statusCode == 404) {
                    notifyHtmlNotFound(bwbId, expressionId);
                }
                var slowUrl = lawlyBot.URL_LAWLY_BOT + bwbId + '/' + expressionId +
                    '/xml/' + bwbId + "_" + expressionId + ".xml";
                var req = https.get(
                    slowUrl, function (res) {
                        koopRepo.getHtmlFromStream(res)
                            .then(onHtmlReceived(_this))
                            .catch(throwError);
                    }
                );
                req.on('error', function (err) {
                    console.error(err);
                    onHtmlReceived(_this)('Kon document niet laden: ' + bwbId + "/" + expressionId);
                })
            })
    },

    getInitialState: function () {
        // naming it initialX clearly indicates that the only purpose
        // of the passed down prop is to initialize something internally
        return {
            loadingDoc: true,
            loadingToc: true,
            htmlSnippet: "",
            toc: []
        };
    },


    render: function () {
        if (this.state.loadingDoc) {
            return <LoadingView />;
        } else if (this.state.error) {
            return <ErrorView error={this.state.error}/>;
        } else {
            return <div>
                <ToC toc={this.state.toc}/>
                <div id="doc-container">
                    <div id="doc-positioner">
                        <div id="doc-margins">
                            <DocContentView html={this.state.htmlSnippet}/>
                        </div>
                    </div>
                </div>
            </div>;
        }
    }
});

module.exports = DocReader;