const React = require('react');
const PureRenderMixin = require('react-addons-pure-render-mixin');
const MenuDocsBurgerlijkWetboek = require('./menu-docs-burgerlijk-wetboek.jsx');
const MenuDocsOftenViewed = require('./menu-docs-often-viewed.jsx');

const BrowserCheck = React.createClass({
    mixins: [PureRenderMixin],

    getDefaultProps: function () {
        return {}
    },

    closeMenu: function () {
        if (this.state.menuOpen) this.setState({menuOpen: false});
    },
    handleClick: function () {
        //console.log("Changing menu state from {open:" +
        //    this.state.menuOpen +
        //    "} to {open:" + !this.state.menuOpen + "}");
        this.setState({
            menuOpen: !this.state.menuOpen
        });
    },

    getInitialState: function () {
        // naming it initialX clearly indicates that the only purpose
        // of the passed down prop is to initialize something internally
        return {
            menuOpen: false
        };
    },

    render: function () {
        var overlay = <div id="menu-bg-overlay"
                           onClick={this.closeMenu}
                           style={{
                       visibility:this.state.menuOpen ?'visible': 'hidden',
                       width: '100%', position: 'fixed', top:0,bottom:0,left:0,right:0,
                           zIndex: 0,
                       background: 'rgba(0,0,0,0.3)'
                     }}
        ></div>;


        return (
            <div>
                {overlay}
                <div id={"quick-menu"} className={this.state.menuOpen?"opened":''}>
                    <div
                        style={{zIndex: 50}}
                        id='quick-menu-content-cntnr'>
                        <div id='quick-menu-content'>
                            <MenuDocsOftenViewed/>
                            <MenuDocsBurgerlijkWetboek/>
                        </div>
                        <a style={{display: 'none'}} href="/all">.</a>
                        <footer id="footer">
                            <p>&copy; 2016  Lawly</p>

                            <p id="contact_us">
                                <a href="mailto:info@lawly.nl">info@lawly.nl</a>
                            </p>
                        </footer>
                    </div>
                    <div style={{zIndex: 50}} id="menu-opener-container">
                        <button onClick={this.handleClick} id="menu-opener"><i className="fa fa-bars"> </i></button>
                    </div>

                </div>
            </div>
        );
    }
});

module.exports = BrowserCheck;